<template>
  <div class="box">
    <el-dialog :title="info.title" :visible.sync="info.show">
  <el-form :model="form">
     <el-form-item label="上级分类" :label-width="formLabelWidth">
      <el-select v-model="form.pid">
        <el-option label="--请选择--" value="" readonly></el-option>
        <el-option label="顶级菜单" :value="0"></el-option>
        <!-- 动态请求数据 -->
        <el-option v-for="item in list" :key="item.id" :label="item.catename" :value="item.id"></el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="分类名称" :label-width="formLabelWidth">
      <el-input v-model="form.catename" autocomplete="off"></el-input>
    </el-form-item>
    <!-- <el-form-item label="图片" :label-width="formLabelWidth">
      <el-upload
        class="avatar-uploader"
        action="#"
        :show-file-list="false"
        :on-change="changeImg">
        <img v-if="imageUrl" :src="imageUrl" class="avatar">
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>
    </el-form-item> -->
    <el-form-item label="图片" :label-width="formLabelWidth">
        <div class="box-img">
          <h3>+</h3>
          <img v-if="imageUrl" :src="imageUrl">
          <input type="file" @change="changeImg1">
        </div>
    </el-form-item>
    <el-form-item label="菜单状态" :label-width="formLabelWidth">
      <el-switch  v-model="form.status" active-color="blue" inactive-color="grey" :active-value="1"
    :inactive-value="2">
</el-switch>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="cancel">取 消</el-button>
    <el-button type="primary" @click="add" v-if="info.isAdd==true">添加</el-button>
    <el-button type="primary" @click="update" v-else>修改</el-button>
  </div>
</el-dialog>

  </div>
</template>

<script>
import {requestCateAdd,requestCateOne,requestCateUpdate} from '../../../util/request';
import {successAlert,warningAlert} from '../../../util/alert';
import {mapGetters, mapActions} from 'vuex';
export default {
  props:["info"],
  computed:{
    ...mapGetters({
      list:"cate/list",
    })
  },
  data() {
      return {
        imageUrl:'',
        form: {
          pid: 0,
          catename:'',
          img: null,
          status:1,
        },
        formLabelWidth: '120px',
        value:true
      };
    },
    methods:{
      // 自定义图片上传
      changeImg1(e){
        var file = e.target.files[0];
        // 处理文件大小
        if(file.size > 2*1024*1024){
          warningAlert('上传图片不能超过2M');
          return;
        }
        // 处理文件后缀
        var fileArr = ['.png','.jpg','.jpeg','.gif'];
       var extName =  file.name.slice(file.name.lastIndexOf('.'));
       if(!fileArr.some((item)=>item === extName)){
         warningAlert('上传文件必须是图片');
         return;
       }

       // 生成URL地址，赋值给imageUrl，使图片展示出来
        this.imageUrl = URL.createObjectURL(file);
        // img属性存放图片的信息
        this.form.img = file;
      },
      changeImg(e){
        // 处理文件大小
        if(e.size > 2*1024*1024){
          warningAlert('上传图片不能超过2M');
          return;
        }
        // 处理文件后缀
        var fileArr = ['.png','.jpg','.jpeg','.gif'];
       var extName =  e.name.slice(e.name.lastIndexOf('.'));
       if(!fileArr.some((item)=>item === extName)){
         warningAlert('上传文件必须是图片');
         return;
       }
        // 获取上传文件
        var file = e.raw;
        // 生成URL地址，赋值给imageUrl，使图片展示出来
        this.imageUrl = URL.createObjectURL(file);
        // img属性存放图片的信息
        this.form.img = file;
        // console.log(this.imageUrl);
      },
      // 重置内容
      empty(){
        this.form = {
          pid: 0,
          catename:'',
          img: null,
          status:1,
        }
      },
      cancel(){
        this.info.show = false
        if(!this.info.isAdd){
          this.empty();
        }
      },
      add(){
        this.info.show = false;
        // 发送axios请求
        requestCateAdd(this.form).then((res)=>{
          if(res.data.code === 200){
            successAlert(res.data.msg);
            //重置form数据
            this.empty();
            //弹框消失
            this.cancel();
             this.requestList();
          }else{
            warningAlert(res.data.msg);
          }
        })

      },
      getDetail(id){
        // 根据ID获取某一调数据
        requestCateOne({id:id}).then(res=>{
          // 将获取的该条数据展示在对话框中
          this.form = res.data.list;
          this.form.id = id;
          this.imageUrl = this.$preImg+res.data.list.img;
        })
      },
      update(){
        // 发送修改请求
        requestCateUpdate(this.form).then(res=>{
          if(res.data.code == 200){
            successAlert('修改成功');
             this.empty()
            this.cancel()
            this.requestList();
          }else{
            warningAlert(res.data.msg);
          }
        })
      },
      ...mapActions({
        requestList:"cate/changeCateActions"
      })
    },


}
</script>

<style scoped lang="stylus">
  .box >>> .el-upload {
    border: 1px dashed #d9d9d9 !important;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .box >>> .el-upload:hover {
    border: 1px dashed #409EFF !important;

  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .box-img{
    width 100px;
    height 100px;
    border:1px dashed lightblue;
    position:relative;
    h3{
      width:100%;
      height 100%;
      line-height 100px;
      text-align:center;
    }
    img{
      width:100%;
      height 100%;
      position:absolute;
      top:0;
      left:0;
    }
    input {
      width:100%;
      height 100%;
      position:absolute;
      top:0;
      left:0;
      opacity:0;
    }
  }
</style>



